import React, { useState } from 'react';
import { useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { isModalOpenState } from '../states/modal';
import { LoginState, userState } from '../states/users';
import { apiLogin } from '../utils/api';
import Signup from './Signup';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 300px;
`;

const InputWrapper = styled.div`
  /* border: 1px solid black; */
  border-radius: 5px;
  width: 80%;
  padding: 0.5rem;
  margin: 0.3rem;
  background-color: ${props => props.color || 'rgb(234 234 234)'};
`;

const Input = styled.input`
  border: none;
  background-color: transparent;
  &:focus {
    outline: none;
  }
`;

const Button = styled.button`
  border: none;
  background-color: transparent;
  width: 100%;
  cursor: pointer;
`;

const SignupText = styled.p`
  text-align: end;
  color: blue;
  cursor: pointer;
`;
function Login() {
  console.log('$LOGIN');
  const setUser = useSetRecoilState(userState);
  const setIsModalOpen = useSetRecoilState(isModalOpenState);
  const [inputs, setInputs] = useState({
    id: '',
    pw: '',
  });
  const setIsLoggedIn = useSetRecoilState(LoginState);

  const { id, pw } = inputs;

  const onChange = e => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };

  const login = async () => {
    console.log('!', id, pw);
    const response = await apiLogin({ userid: id, password: pw });
    const { userid: userId, accessToken: token } = response.data;
    localStorage.setItem('token', JSON.stringify({ userId, token }));
    setUser(userId);
    setIsLoggedIn(true);
    setIsModalOpen('');
    console.log(response);
  };

  const onOpenSignupModal = () => {
    setIsModalOpen('signup');
  };

  return (
    <Wrapper>
      <InputWrapper>
        <Input placeholder="" value={id} name="id" onChange={onChange} />
      </InputWrapper>
      <InputWrapper>
        <Input
          placeholder=""
          value={pw}
          name="pw"
          onChange={onChange}
          type="password"
        />
      </InputWrapper>
      <InputWrapper color="orange">
        <Button type="button" onClick={login}>
          
        </Button>
      </InputWrapper>
      <SignupText onClick={onOpenSignupModal}></SignupText>
    </Wrapper>
  );
}

export default Login;
